import React, { createContext, useRef, useState } from "react";
export let Provider = createContext();
function Context({ children }) {
  let [params, setParams] = useState(false);
  let [body, setBody] = useState(false);
  let [query, setquery] = useState(false);
  let [header, setHeader] = useState(false);
  let [type, setType] = useState("get");
  let [keyValues, setkeyValues] = useState({
    key1: "",
    key2: "",
    key3: "",
  });
  let [valValues, setvalValues] = useState({
    key1: "",
    key2: "",
    key3: "",
  });
  let [headerVal,setHeaderVal]=useState({

  })
  let [paramsValues, setParamsValues] = useState([]);

  return (
    <Provider.Provider
      value={{
        params,
        setParams,
        setBody,
        setType,
        body,
        type,
        keyValues,
        setkeyValues,
        query,
        setquery,
        header,
        setHeader,

        valValues,
        setvalValues,
        paramsValues,
        setParamsValues,
        headerVal,
        setHeaderVal
      }}
    >
      {children}
    </Provider.Provider>
  );
}

export default Context;
