import { makeStyles } from "@material-ui/styles";
import CloseIcon from "@mui/icons-material/Close";
import {
  Alert,
  Autocomplete,
  Button,
  Checkbox,
  CircularProgress,
  FormControlLabel,
  Grid,
  Paper,
  Snackbar,
  Stack,
  Alert as AL,
  TextField,
  IconButton,
  Tooltip,
} from "@mui/material";
import Warning from "./Warning";
import axios from "axios";
import React from "react";
import { useState } from "react";
import { useRef } from "react";
import { useContext, useEffect } from "react";
import { Provider } from "./Context";
import Table from "./Table";
import SendIcon from "@mui/icons-material/Send";
import { Typography } from "@mui/material";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
let usestyles = makeStyles({
  input: {
    width: "70%",
    height: 40,
    paddingLeft: "20px",
    fontSize: "17px",
    letterSpacing: "0.6px",
    fontWeight: 500,
    caretColor: "#ad1457",
    borderRadius: "20px",
    outline: "none",
    paddingRight: "15px",
    border: "2px solid #ad1457 !important",
  },
  button: {
    height: 40,
    textTransform: "capitalize",
    color: "#fff !important",
    borderRadius: "10px !important",
    width: "120px",
  },
});
function Home() {
  let {
    params,
    setParams,
    setBody,
    setType,
    body,

    query,
    setquery,

    valValues,
    keyValues,
    paramsValues,
    headerVal,
    type,
    header,
    setHeader,
  } = useContext(Provider);
  let classes = usestyles();
  let [copied, setCopied] = useState(false);
  let [url, setUrl] = useState("");
  let [loader, setLoader] = useState(false);
  let [openWarning, setOpenWarning] = useState(false);
  let [response2, setresponse2] = useState("");
  let [count, setCount] = useState(0);
  let [baseUrl, setBaseUrl] = useState("");
  let [urls, setUrls] = useState(
    JSON.parse(localStorage?.getItem("urls")) || []
  );
  let [delIndex, setDelIndex] = useState(null);
  let [_, setDelIitem] = useState(null);
  let [openSnackBar, setOpenSnackBar] = useState(false);
  let [copied2, setCopied2] = useState(false);
  let scrollref = useRef();
  let inpRef = useRef();
  function typeChange(_, val) {
    if (val == "get") {
      setBody(false);
      setType(val);
      return;
    }
    setType(val);
  }
  function paramsChange() {
    setParams(!params);
    setBody(false);
    setquery(false);
    setHeader(false);
  }
  function bodyChange() {
    setBody(!body);
    setParams(false);
    setquery(false);
    setHeader(false);
  }
  function queryChange() {
    setquery(!query);
    setBody(false);
    setParams(false);
    setHeader(false);
  }
  function changeUrl(e) {
    setUrl(e.target.value);
  }
  function submit(e) {
    let obj = {};
    let obj2 = {};
    let pair = [];
    let Shifted;
    e.preventDefault();

    if (url == "") {
      inpRef.current.focus();
      return;
    }
    setLoader(true);
    if (body || query) {
      let arr = Object.values(keyValues);
      let arrvalues = Object.values(valValues);

      if (body) {
        arr.filter((item, i) => {
          let index = i;
          if (item == "") {
            index = index - 1;

            return;
          }
          let sameKey = arr.indexOf(item) !== i;
          if (sameKey) {
            Shifted = item;

            pair.push(arrvalues[i]);

            if (i == index) {
              pair.unshift(arrvalues[arr.indexOf(item)]);

              obj[item] = pair;
            }
          } else {
            obj = {
              ...obj,
              [keyValues["key" + (i + 1)]]: arrvalues[i],
            };
          }
        });
      }
      for (let i = 0; i < Object.keys(keyValues).length; i++) {
        let _ =
          keyValues["key" + (i + 1)] == "" || valValues["key" + (i + 1)] == ""
            ? null
            : (obj[keyValues["key" + (i + 1)]] = valValues["key" + (i + 1)]);
      }
    }

    if (query) {
      let querykeys = Object.keys(obj);
      let queryvalues = Object.values(obj);

      if (querykeys.length <= 0 || queryvalues.length <= 0) {
        setUrl(url);
      } else {
        let second;
        querykeys.forEach((item, i) => {
          if (i == 0) {
            second = `?${querykeys[0]}=${queryvalues[0]}`;

            return;
          }

          second =
            second !== undefined || null
              ? second + `&${querykeys[i]}=${queryvalues[i]}`
              : `&${querykeys[i]}=${queryvalues[i]}`;
        });

        setUrl(url.split("?")[0] + second);
      }
    }
    if (params) {
      let demoUrl = null;

      if (count === 0) {
        setBaseUrl(url);
        setCount(1);
      }
      setUrl(baseUrl);

      var _ = paramsValues.filter(function (el) {
        if (el != null || undefined || "") {
          demoUrl =
            demoUrl != undefined || null || "" ? demoUrl + `/${el}` : `/${el}`;

          return;
        }
      });

      if (count == 0) {
        if (demoUrl == null) {
          setUrl(url);
        } else {
          setUrl(url + demoUrl);
        }
      } else {
        if (demoUrl == null) {
          setUrl(baseUrl);
        } else {
          setUrl(baseUrl + demoUrl);
        }
      }
    }
    if (header) {
      let arrKeys = Object.values(keyValues);
      let arrvalues = Object.values(valValues);
      let obj = {};
      arrKeys.forEach((item, i) => {
        if (
          item === "" ||
          undefined ||
          null ||
          arrvalues[i] == "" ||
          undefined ||
          null
        ) {
          return;
        }
        obj[item] = arrvalues[i];
      });
      obj2 = obj;
 
    }

    if(Object.keys(headerVal).length>0){
      obj2={
        ...obj2,
        ...headerVal
      }
      console.log(obj2)
 
    
    }
    axios({
      method: type,
      url: url,
      headers: header ||Object.keys(headerVal).length>0 ? obj2 : null,
      data:
        body && Object.keys(obj).length > 0
          ? {
              ...obj,
              [Shifted]: pair,
            }
          : null,
    })
      .then((res) => {
        setresponse2(JSON.stringify(res.data));
        setCopied2(false);
        setLoader(false);
        let demo = [...urls];

        demo.push(url);

        localStorage.setItem(
          "urls",
          JSON.stringify([...demo, inpRef.current.value])
        );
        setUrls(demo);
        demo = [];

        scrollref.current?.scroll({
          top: 0,
          left: 200,
          behavior: "smooth",
        });
      })
      .catch((err) => {
        let demoUrls = [...urls, url];
        setresponse2(JSON.stringify(err));
        localStorage.setItem("urls", JSON.stringify(demoUrls));
        demoUrls = [];
        setCopied2(false);
        setLoader(false);
        let demo = [...urls];
        demo.push(url);
        setUrls(demo);
        demo = [];

        scrollref.current?.scroll({
          top: 0,
          left: 200,
          behavior: "smooth",
        });
      });
  }
  function deleteUrls(i) {
    setDelIndex(i);
    setOpenWarning(true);
  }
  useEffect(() => {
    const unloadCallback = (event) => {
      event.preventDefault();
      event.returnValue = "";

      return "";
    };

    window.addEventListener("beforeunload", unloadCallback);
    return () => window.removeEventListener("beforeunload", unloadCallback);
  }, []);
  function headerChange() {
    setHeader(!header);
    setParams(false);
    setBody(false);
    setquery(false);
  }

  return (
    <>
      {urls.length > 0 && (
        <Stack
          ref={scrollref}
          mt={0.5}
          sx={{
            overflowX: "scroll",
            whiteSpace: "nowrap",
            "&::-webkit-scrollbar": {
              height: "5px",
            },
            "&::-webkit-scrollbar-thumb": {
              backgroundColor: "apier.main",
              borderRadius: 1,
            },
          }}
          width="100%"
          height={60}
          direction={"row"}
        >
          {urls.map((item, i) => (
            <Paper
              sx={{
                height: "40px",
                mr: 0.2,
                cursor: "pointer",
              }}
              key={i}
              component="div"
              onClick={() => {
                setUrl(item);
              }}
            >
              <Stack
                direction={"row"}
                width={200}
                height={40}
                alignItems={"center"}
                justifyContent="space-between"
                position={"relative"}
                p={0.5}
              >
                <Typography
                  fontSize={12}
                  height={20}
                  fontWeight={550}
                  width="80%"
                  textAlign={"center"}
                  letterSpacing={0.9}
                  sx={{
                    overflow: "hidden",
                    overflowX: "scroll",
                    "&::-webkit-scrollbar": {
                      display: "none",
                    },
                  }}
                >
                  {item}
                </Typography>
                <Button
                  onClick={() => deleteUrls(i)}
                  sx={{
                    minWidth: "auto",
                    width: "30px",
                  }}
                >
                  <CloseIcon
                    fontSize="small"
                    color="apier"
                    sx={{
                      cursor: "pointer",
                    }}
                  />
                </Button>
              </Stack>
            </Paper>
          ))}
        </Stack>
      )}
      <Stack
        direction={"row"}
        mt={urls.length > 0 ? 2 : 3}
        spacing={1}
        onSubmit={submit}
        component="form"
        justifyContent={"center"}
      >
        <Autocomplete
          disablePortal
          defaultValue={"get"}
          options={["get", "post", "patch", "put", "delete"]}
          onChange={typeChange}
          value={type}
          sx={{
            width: {
              md: 150,
              sm: 100,
              xs: 80,
            },
            cursor: "pointer",
          }}
          renderInput={(params) => (
            <TextField
              sx={{
                borderRadius: "15px !important",
              }}
              {...params}
              color="apier"
              size="small"
            />
          )}
        />
        <input
          className={classes.input}
          type="search"
          value={url}
          onChange={changeUrl}
          ref={inpRef}
          onInput={(e) => {
            if (e.target.value == "" || e.target.value < baseUrl.length) {
              setCount(0);
            }
          }}
        />
        <Button
          color="apier"
          variant="contained"
          type="submit"
          className={classes.button}
          endIcon={
            loader ? (
              <CircularProgress color="inherit" size={"20px"} />
            ) : (
              <SendIcon />
            )
          }
        >
          send
        </Button>
      </Stack>
      <Stack mt={2} alignItems={"center"}>
        <Stack width="80%" direction={"row"} spacing={1}>
          <FormControlLabel
            control={
              <Checkbox
                onChange={paramsChange}
                checked={params}
                color="apier"
                size="medium"
              />
            }
            label="params"
          />
          <FormControlLabel
            control={
              <Checkbox
                onChange={headerChange}
                checked={header}
                color="apier"
                size="medium"
              />
            }
            label="headers"
          />
          <FormControlLabel
            control={
              <Checkbox
                onChange={queryChange}
                checked={query}
                color="apier"
                size="medium"
              />
            }
            label="query"
          />
          {type !== "get" && type !== null && (
            <FormControlLabel
              control={
                <Checkbox
                  onChange={bodyChange}
                  checked={body}
                  color="apier"
                  size="medium"
                />
              }
              label="body"
            />
          )}
        </Stack>
      </Stack>
      <Grid container justifyContent={"center"} alignItems="center">
        {header && (
          <Grid item xs={12} md={5} sm={11}>
            <Table />
          </Grid>
        )}
        {query && (
          <Grid item xs={12} md={5} sm={11}>
            <Table />
          </Grid>
        )}
        {body && (
          <Grid item xs={12} md={5} sm={11}>
            <Table />
          </Grid>
        )}
        {params && (
          <Grid item xs={12} md={5} sm={11}>
            <Table />
          </Grid>
        )}

        <Grid
          item
          xs={12}
          justifyContent="center"
          alignItems={"center"}
          display="flex"
          md={body || query || params || header ? 6.7 : 10}
          sm={11}
        >
          <Paper
            sx={{
              height: "350px",
              width: "95%",
              mt: {
                sm: 8,
                xs: 8,
                md: 5,
              },
              padding: 4,
              letterSpacing: 1,
              lineHeight: 3,
              overflow: "hidden",
              wordBreak: "break-all",
              overflowY: "scroll",
              "&::-webkit-scrollbar": {
                display: "none",
              },
            }}
          >
            {response2.length > 0 && (
              <Stack mb={1} alignItems={"flex-end"}>
                <Tooltip
                  title="copy"
                  placement="left"
                  leaveDelay={1000}
                  enterDelay={0}
                >
                  <IconButton
                    onClick={() => {
                      setCopied(true);
                      setCopied2(true);
                      navigator.clipboard.writeText(response2);
                    }}
                  >
                    <ContentCopyIcon
                      color={copied2 ? "apier" : "inherit"}
                      sx={{
                        cursor: "pointer",
                      }}
                    />
                  </IconButton>
                </Tooltip>
              </Stack>
            )}
            {response2}
          </Paper>
        </Grid>
      </Grid>
      <Warning
        setOpenSnackBar={setOpenSnackBar}
        setDelItem={setDelIitem}
        delIndex={delIndex}
        urls={urls}
        setUrls={setUrls}
        openWarning={openWarning}
        setOpenWarning={setOpenWarning}
      />
      <Snackbar
        autoHideDuration={2000}
        onClose={(_, e) => {
          if (e === "timeout") {
            setOpenSnackBar(false);
            return;
          }
        }}
        open={openSnackBar}
        sx={{
          overflow: "hidden",
          height: 60,
          width: 400,
          borderRadius: 2,
          "&::-webkit-scrollbar": {
            display: "none",
          },
        }}
      >
        <AL
          sx={{
            color: "#fff",
            maxWidth: 400,
          }}
          variant="filled"
        >
          <Typography> successfully deleted</Typography>
        </AL>
      </Snackbar>
      <Snackbar
        autoHideDuration={2000}
        onClose={(_, e) => {
          if (e === "timeout") {
            setCopied(false);
            return;
          }
        }}
        open={copied}
      >
        <AL
          sx={{
            color: "#fff",
            width: 150,
          }}
          variant="filled"
          icon={<ContentCopyIcon />}
        >
          <Typography>copied</Typography>
        </AL>
      </Snackbar>
    </>
  );
}

export default Home;
