import React from "react";
import {
  Button,
  Paper,
  SpeedDial,
  SpeedDialAction,
  SpeedDialIcon,
  Stack,
  Table as Tb,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
  Tooltip,
  Typography,
} from "@mui/material";
import { makeStyles } from "@material-ui/styles";
import AddIcon from "@mui/icons-material/Add";
import { useState } from "react";
import { useContext } from "react";
import { Provider } from "./Context";
import { useEffect } from "react";
let usestyles = makeStyles({
  TableCell: {
    borderBottom: "none !important",
  },
});
function Table() {
  let classes = usestyles();

  let [tableRows, setTableRows] = useState([1, 1, 1]);
  let {
    params,

    keyRef,
    body,
    keyValues,
    setkeyValues,
    valValues,
    setvalValues,
    query,
    paramsValues,
    setParamsValues,
    header,

    setHeaderVal
  } = useContext(Provider);
  function addRows() {
    setTableRows([...tableRows, 1]);
  }
  useEffect(() => {
    return () => {
      if(header){

        let obj={}
     let arrkeys=document.querySelectorAll(".key input")
     let arrvals=document.querySelectorAll(".value input")

     arrkeys.forEach((item,i)=>{
     
      if((item.value==""||null||undefined)||(arrvals[i].value==""||null||undefined)){
return
      }
      console.log(      arrkeys[i].value,arrvals[i].value)
      obj[item.value]=arrvals[i].value

     })
     setHeaderVal(obj)
      }
      setkeyValues({
        key1: "",
        key2: "",
        key3: "",
      });
      setvalValues({
        key1: "",
        key2: "",
        key3: "",
      });
      setParamsValues([]);
    };
  }, []);
  return (
    <>
      <Stack width="100%" alignItems={"center"} mt={0}>
        <Typography
          fontSize={"20px"}
          letterSpacing="0.7px"
          color="apier.main"
          fontWeight="bold"
          gutterBottom
          variant="caption"
        >
          {query && "query"}
          {params && "params"}
          {body && "body"}
          {header&& "headers"}
        </Typography>

        <Paper
          variant="outlined"
          sx={{
            width: "95%",
            height: "auto",
            maxHeight: "350px",
            overflow: "hidden !important",
            overflowY: "scroll !important",
            "&::-webkit-scrollbar": {
              display: "none",
            },
          }}
        >
          <Tb
            stickyHeader
            sx={{
              width: "100%",
            }}
          >
            <TableHead>
              <TableRow>
                {!params && <TableCell align="center">key</TableCell>}
                <TableCell align="center">value</TableCell>
              </TableRow>
            </TableHead>
            <TableBody ref={keyRef}>
              {tableRows.map((_, i) => (
                <TableRow key={i}>
                  <TableCell className={classes.TableCell}>
                    <TextField
                      size="small"
                      placeholder={params ? "value" : "key"}
                      className="key"
                      color="apier"
                      value={
                        params ? paramsValues[i] : keyValues["key" + (i + 1)]
                      }
                      onChange={(e) => {
                        if (params) {
                          let demo = [...paramsValues];
                          demo[i] = e.target.value;
                          setParamsValues(demo);
                          return;
                        }

                        setkeyValues({
                          ...keyValues,
                          ["key" + (i + 1)]: e.target.value,
                        });
                      }}
                      sx={{
                        width: "100%",
                      }}
                    />
                  </TableCell>
                  {!params && (
                    <TableCell className={classes.TableCell}>
                      <TextField
                        className="value"
                        color="apier"
                        size="small"
                        placeholder="value"
                        sx={{
                          width: "100%",
                        }}
                        value={valValues["key" + (i + 1)]}
                        onChange={(e) => {
                          setvalValues({
                            ...valValues,
                            ["key" + (i + 1)]: e.target.value,
                          });
                        }}
                      />
                    </TableCell>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Tb>
        </Paper>
        <Tooltip
          arrow
          enterDelay={200}
          leaveDelay={500}
          placement="right"
          title="add"
        >
          <Button
            variant="contained"
            color="apier"
            sx={{
              borderRadius: "50%",
              width: "50px",
              height: "50px",
              position: "absolute",
              right: "3%",
              bottom: {
                sm: "60px !important",
                xs: "60px !important",
                md: "40px !important",
              },
            }}
            onClick={addRows}
          >
            <AddIcon
              fontSize="medium"
              sx={{
                fill: "#fff",
              }}
            />
          </Button>
        </Tooltip>
      </Stack>
    </>
  );
}

export default Table;
