import { makeStyles } from "@material-ui/styles";
import { Button, Modal, Paper, Stack, Typography } from "@mui/material";

import React from "react";
let usestyles = makeStyles({
  paperStyle: {
    border: "none !important",
    paddingBottom: "10px",
  },
  modalStyle: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  buttonStyle: {
    color: "#fff !important",
    letterSpacing: "0.7px !important",
    fontSize: "15px !important",
    textTransform: "capitalize",
  },
});
function Alert({
  setOpenSnackBar,
  urls,
  setUrls,
  openWarning,
  setOpenWarning,
  delIndex,
  setDelItem,
}) {
  let classes = usestyles();
  function cancel() {
    setOpenWarning(false);
  }
  function deleteUrls() {
    let demo = [...urls];
    setDelItem(demo.splice(delIndex, 1));
    localStorage.setItem("urls",JSON.stringify(demo))
    setUrls(demo);
    setOpenWarning(false);
    setOpenSnackBar(true);
  }
  return (
    <>
      <Modal hideBackdrop open={openWarning} className={classes.modalStyle}>
        <Paper
          variant="elevation"
          className={classes.paperStyle}
          sx={{
            width: {
              md: 400,
              sm: "80%",
              xs: "90%",
            },

            maxHeight:400,
            height:"auto",
            overflow:"hidden",
            overflowY:"scroll",
            "&::-webkit-scrollbar": {
              display: "none",
            },
          }}
        >
          <Stack alignItems={"center"}>
            <Typography
             pr={1} pl={1}
              textAlign={"center"}
              fontSize={18}
              color="apier.main"
              gutterBottom
              mt={1.5}
              sx={{
                wordBreak:"break-all"
              }}
            >
              {" "}
              do you want delete {urls[delIndex]} ?
            </Typography>
          </Stack>
          <Stack
            direction={"row"}
            justifyContent="flex-end"
            spacing={2}
            pr={1}
            mt={2}
          >
            <Button
              color="apier"
              className={classes.buttonStyle}
              variant="contained"
              onClick={cancel}
            >
              cancel
            </Button>
            <Button
              color="apier"
              className={classes.buttonStyle}
              variant="contained"
              onClick={deleteUrls}
            >
              yes
            </Button>
          </Stack>
        </Paper>
      </Modal>
    </>
  );
}

export default Alert;
