import { createTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@mui/material";
import React, { useState } from "react";
import Home from "./Shared/Home";
import Nav from "./Shared/Nav";
import Context from "./Shared/Context";
import { BrowserRouter as Br, Route, Routes,HashRouter } from "react-router-dom";
import WelcomeScreen from "./Shared/WelcomeScreen";
function Apps() {
  document.title = "Apier";
  let theme = createTheme({
    palette: {
      apier: {
        main: "#ad1457",
      },
    },
  });
  let [hideWelcome,SetHideWelcome]=useState(false)
  setTimeout(() => {
    SetHideWelcome(true)
  }, 3000);
  return (
    <>
      <Context>
        <ThemeProvider theme={theme}>
          <HashRouter>
            <Routes>
            {/* <Route
                path="/"
                element={
                  <>
            {
              hideWelcome?  <>
              <Nav />
              <Home />
            </>:     <WelcomeScreen/>
            }
               
                  </>
                }
              /> */}
               <Route
                path="/"
                element={
                  <>
            {
             <>
              <Nav />
              <Home />
            </>
            }
               
                  </>
                }
              />
            </Routes>
          </HashRouter>
        </ThemeProvider>
      </Context>
    </>
  );
}

export default Apps;
